import { mix, getEasingForSegment, removeItem } from '@motionone/utils';

function eraseKeyframes(sequence, startTime, endTime) {
  for (var i = 0; i < sequence.length; i++) {
    var keyframe = sequence[i];

    if (keyframe.at > startTime && keyframe.at < endTime) {
      removeItem(sequence, keyframe); // If we remove this item we have to push the pointer back one

      i--;
    }
  }
}

function addKeyframes(sequence, keyframes, easing, offset, startTime, endTime) {
  /**
   * Erase every existing value between currentTime and targetTime,
   * this will essentially splice this timeline into any currently
   * defined ones.
   */
  eraseKeyframes(sequence, startTime, endTime);

  for (var i = 0; i < keyframes.length; i++) {
    sequence.push({
      value: keyframes[i],
      at: mix(startTime, endTime, offset[i]),
      easing: getEasingForSegment(easing, i)
    });
  }
}

export { addKeyframes, eraseKeyframes };